.navbar {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  /* max-width: 1500px; */

}

.navbar-logo {
  color: #ff6d99;
  font-size: 2rem;
  text-decoration: none;
  font-family: "Shadows Into Light", cursive;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: left;
}


.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 20px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding-inline: 0.9rem;
  font-family: "Montserrat", sans-serif;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    padding-top: 40px;
    justify-content: start;
    width: 80%;
    height: 80vh;
    position: absolute;
    top: 60px;
    left: 100%;
    opacity: 0.8;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 0.8;
    left: 20%;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    /* text-align: right;
      top: 50%;
      transform: translate(0, -50%);
      width: 80%;
      display: table;
      align-items: center; */

    display: flex;
    width: 80%;
    justify-content: right;
    align-items: center;
    /* border: 3px solid green; */

  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
    opacity: 1;
  }

  .navbar-logo {
    position: absolute;
    /* top: 0;
      left: 0; */
      
    /* transform: translate(25%, 50%); */
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    /* display: block; */
    text-align: center;
    vertical-align: text-top;
    margin: 2rem auto;
    border-radius: 4px;
    width: 100%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    /* padding: 14px 20px; */
    /* border: 1px solid #fff; */
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}

body, html {
  overflow-x: hidden;
}